@mixin bg-variant($parent, $color, $ignore-warning: false) {
    #{$parent} {
        background-color: $color !important;
        color: color-yiq($color);

        // old -         -      
        // $text-color: color-yiq($color);
        // @if $text-color == #fff {
        //     color: $text-color
        // }
    }
    a#{$parent},
    button#{$parent} {
        @include hover-focus() {
            background-color: darken($color, 10%) !important;
        }
    }
}